define('wajax',['jquery','modules/ui/showMsg/js/main'],function($,showError){
	/**
	 * communicate with backend through jquery ajax. 
	 * @requires jquery 
	 * @requires modules/ui/showMsg/js/main 
	 * @exports modules/net/wAjax 
	 * @param {String} settings.url -  url.
	 * @param {String} settings.type="post" -  http getpost.
	 * @param {callback} settings.error - .
	 * @param {callback} settings.success - .
	 * @param {callback} settings.complete - .
	 */
	var wAjax = function(settings) {
		function callback(obj) {
			var code = obj['code'];
			if( code == undefined) {
				showError({
					msg: ''
				});
				settings.generalError&&settings.generalError(obj);
			} else if(code == 0) {
				settings.success && settings.success(obj);		
			} else {
				if( settings.error == undefined ) {
					showError(obj);
					settings.generalError&&settings.generalError(obj);
				} else {

					/**
					 * @callback callback
					 * @param {Number} obj.code -  
					 * @param {String} obj.msg -  
					 * @param {*} obj.data -  
					 */
					settings.error(obj);
				}
			}
		}

		$.ajax({
			url: settings.url || window.location.href,
			type: settings.type || "post",
			data: settings.data,
			traditional:settings.traditional || true,
			dataType: "json",
			beforeSend:function(request){request.setRequestHeader("requestType", "ajax");},
			success: callback,
			error: function(xhr, status) {
				var map = {
					"abort": {
						code: -1,
						msg: ""
					},
					"parsererror": {
						code: -2,
						msg: ""
					},
					"timeout": {
						code: -3,
						msg: ""
					},
					"error": {
						code: -4,
						msg: ""
					}
				};
				var result = map[status];
				if (!result) {
					result = {
						code: -5,
						msg: ""
					};
				}
				showError(result);
				settings.generalError&&settings.generalError(result);
			},
			complete: settings.complete	
		});	
	};
	return wAjax;
});

